* {
  box-sizing: border-box;
}

body, html {
  margin: 0px;
  padding: 0px;
  font-family: Arial, Helvetica, sans-serif;
  background:yellow;
  min-height:100dvh;
  width:100%;
  
}

.app {
  height:400px;
  width:100%;
  background: red;
  position:relative;
}

.shapesIcon {
  position: absolute;
  top: -52px;
  left: -54px;
  width: 235px;
  height: 173px;
}

.shapesIcon1 {
  position: absolute;
  height: 20.5%;
  width: 60.26%;
  right: -18.97%;
  bottom: -4.74%;
}

.page-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  min-width: 100%;
  justify-content: center;
  position: relative;
  padding: 0;
  background: red;
  height:100dvh;
  display:none;
}

:root {
  --pr-color: #32cd32;
  --sc-color: rgb(80, 128, 96);
}

